@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');

*{
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
    box-sizing: border-box;
}

body{
    background-color: rgb(52, 57, 66);
    color: rgb(36, 36, 36);
    padding: 12px;

}



/*--------------------------------------------------------------------*/
.app{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 10px auto;
    background-size: cover;
    text-align: center;
    max-width: 450px;
    min-height: 95vh;
    border-radius: 2rem;
    overflow: hidden;
    padding: 0px 30px;
    background-image: url("assets/clouds-2.jpg");
    background-position: bottom right;
    transition: 0.5s ease-out;
}
/*--------------------------------------------------------------------*/

.cold{
    background-image: url("assets/cold-animated-landscape.jpeg");
    filter: brightness(80%);
}

.warm{
    background-image: url("assets/landscape.jpeg");
    filter: brightness(80%);
}



/*--------------------------------------------------------------------*/
.search-box{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
}

.search-bar{
    background-color: rgba(255, 255, 255, 0.801);
    border: none;
    outline: none;
    padding: 10px;
    border-radius: 0.5rem;
    color: rgb(0, 0, 0);
    margin-right: 5px;
}

.search-bar::placeholder{
    text-align: center;
}

.search-button{
    background-color: rgba(44, 45, 56, 0.836);
    border: none;
    outline: none;
    padding: 10px;
    border-radius: 0.5rem;
    color: white;
}

.search-button:hover{
    background-color: rgb(44, 45, 56);
}

/*--------------------------------------------------------------------*/

.location-box{
    padding: 20px;
    margin: 20px;
    background-color: rgba(255, 255, 255, 0.39);
    border-radius: 0.9rem;
    max-width: 300px;
}

.location-box .location{
    font-size: 20px;
    line-height: 2rem;
    font-weight: 700;
}

.location-box .date{
    font-style: italic;
}
/*--------------------------------------------------------------------*/


.weather-box .temperature{
    font-weight: 900;
    font-size: 3rem;
    background-color: rgba(255, 255, 255, 0.336);
    border-radius: 0.9rem;
    padding: 20px;
    margin: 20px 70px;
}

.weather-box .weather{
    text-align: center;
    border-radius: 0.9rem;
    padding-bottom: 10px;
    font-weight: 700;
    font-size: 1.5rem;
    text-shadow: none;
}

/*--------------------------------------------------------------------*/

.message{
    color: black;
    padding: 15px;
    border-radius: 1rem;
    font-size: 0.8rem;
    font-weight: 900;
    margin-top: 5px;
    background-color: rgba(255, 255, 255, 0.301);
}

.phrase{
    background-color: rgba(255, 255, 255, 0.274);
    padding: 10px;
    border-radius: 1rem;
    margin-top: 320px;
    font-weight: 700;
    font-size: 1rem;
}